html, body, #root, .ant-layout {
    background-color: #ecedf5 !important;
    height: 100% !important;
    width: 100% !important;
}

#total {
    font-size: 6rem;
    padding: 0;
    margin: 0
}

.add-item {
    font-size: 3rem;
    margin-bottom: 70px;
    margin-top: 20px
}


.ant-modal-confirm-body i {
    display: none;
}

.ant-modal-confirm-title {
    text-align: center
}

.ant-modal-body {
    padding: 0 !important
}

.ant-modal-content {
    border-radius: 0 !important;
}

.ant-modal-confirm-title {
    margin: 20px;
    font-size: 1.2rem !important
}

.ant-modal-confirm-content {
    display: none
}

.ant-modal-confirm-btns {
    width: 100%;
    margin: 0 !important;
}

.ant-modal-confirm-btns .ant-btn, 
.ant-modal-confirm-btns .ant-btn:hover,
.ant-modal-confirm-btns .ant-btn:focus {
    border: none;
    background-color: #363636;
    color: white;
    width: 100% !important;
    border-radius: 0 !important;
    height: 40px;
    font-size: 1.2rem !important
}

.bottom-fixed {
    background-color: #363636 !important;
    color: #ecedf5 !important;
    position: fixed !important;
    left: 0;
    bottom: 0 !important;
    height: 50px !important;
    border: none !important;
    outline: 0 !important;
    -webkit-box-shadow: none !important;
    box-shadow: none !important;
    border-radius: 0 !important;
    font-size: 1.2rem !important;
    padding: 10px !important
}

.random-item {
    font-size: 1.4rem !important;
}

.seen > .ant-card-body {
    background: #363636;
    color: white
}

.ant-btn {
    border-radius: none !important;
}

.ant-layout-content {
    padding: 10px;
    text-align: center
}

.ant-card {
    margin: 10px 0 !important;
}

.ant-input {
    border: none !important;
    outline: 0 !important;
    -webkit-box-shadow: none !important;
    box-shadow: none !important;
    border-radius: 0 !important;
    font-size: 1.2rem !important
}

.ant-input-wrapper, .ant-input-group .ant-input-group-addon{
    border-radius: 0 !important;
    border-top-right-radius: 1px !important;
}

.ant-input-group-addon {
    border: none !important;
    background: red !important;
    color: white !important;
    border-radius: 0 !important
}

.ant-card-actions > li {
    margin: 0 !important;
    padding: 10px 0 !important;
}

.ant-card-actions > li span{
    font-size: 1.4rem;
    color: red
}